import React from "react";
import { NavLink } from "react-router-dom";
import dubaiVhamberImg from "../../assets/images/articles/dubai-chambers-16000-new-firms-on-board-1.jpg";
import { Helmet } from "react-helmet";

const DubaiChamberNewFirms = () => {
  return (
    <div>
      <Helmet>
      <title>16,000 New Firms Came On Board At Dubai Chamber In 2015</title>
<meta name="keywords" content="dubai chamber of commerce, new firms 2015, dubai uae chambers"/>
<meta name="description" content="In 2015, the emirate of Dubai added over 16,000 new companies to the membership of Dubai Chamber of Commerce."/>
<link rel="canonical" href="https://www.angelgulfjobs.com/dubai-chambers-16k-new-firms-on-board"/>
<meta name="revisit-after" content="7 days"/>
<meta name="robots" content="index, follow"/>
      </Helmet>
      <div className="page-content">
        {/* INNER PAGE BANNER */}
        <div
          className="wt-bnr-inr overlay-wraper bg-center"
          style={{ backgroundImage: `url(${dubaiVhamberImg})` }}
        >
          <div className="overlay-main site-bg-white opacity-01" />
          <div className="container">
            <div className="wt-bnr-inr-entry">
              <div className="banner-title-outer">
                <div className="banner-title-name background-heading p-3">
                  <h2 className="wt-title darkblueTxt">
                    16,000 New Firms Came On Board At Dubai Chamber In 2015
                  </h2>
                </div>
              </div>
              {/* BREADCRUMB ROW */}
              <div>
                <ul className="wt-breadcrumb breadcrumb-style-2">
                  <li>
                    <NavLink to="/">Home</NavLink>
                  </li>
                  <li>
                    16,000 New Firms Came On Board At Dubai Chamber In 2015
                  </li>
                </ul>
              </div>
              {/* BREADCRUMB ROW END */}
            </div>
          </div>
        </div>
        {/* INNER PAGE BANNER END */}
        {/* Employer Detail START */}
        <div className="section-full  p-t20 p-b40 bg-white">
          <div className="container">
            <div className="section-content">
              <div className="row d-flex justify-content-center">
                <div className="col-lg-10 col-md-12">
                  {/* Candidate detail START */}
                  <div className="cabdidate-de-info">
                    {/* <h4 className="twm-s-title">About Company</h4> */}
                    <p>
                      Dh286 billion is the value of exports and re-exports of
                      members during 2016
                    </p>

                    <p>
                      Most vacancies open for applications until end of March of
                      this year
                    </p>

                    <p>
                      In 2015, the emirate of Dubai reinforced its
                      attractiveness to global investors with the addition of
                      over 16,000 new companies joining the membership of Dubai
                      Chamber of Commerce and Industry and taking the
                      organisation’s total membership to 185,000.
                    </p>
                    <p>
                      This 9.5% rise in membership enhances the Chamber’s
                      position as one of the largest membership-based chambers
                      of commerce in the world.
                    </p>

                    <p>
                      The members’ contribution is reflected in their annual
                      exports and re-exports figures which valued at Dh286
                      billion in 2015 with Saudi Arabia emerging as the top-most
                      destination for the members’ exports and re-exports
                      amounting to Dh105.7 billion, or 37% of the total in 2015,
                      while the Chamber registered a 5% growth in the number of
                      Certificates of Origin issued during the past year,
                      totalling 930,000 compared to 887,000 COs issued in 2014.
                    </p>

                    <p>
                      Hamad Buamim, President and CEO, Dubai Chamber revealed
                      today that Dubai's economy emerged resilient during the
                      year 2015 despite the global challenges created by the
                      drop in oil prices, the economic uncertainties in Europe,
                      China and Russia and weak global demand for commodities,
                      mainly due to the emirate’s diversified economy which
                      greatly minimised the impact of the global slowdown’s
                      negative effects on its various economic sectors.
                    </p>
                    <p>
                      Buamim further stressed that Dubai’s economy achieved
                      positive results in the area of aviation, air cargo and
                      tourism which have grown over the past year, while trading
                      activities remained stable despite the challenges.
                    </p>

                    <p>
                      He said that the UAE has already diversified its economy
                      by strengthening the non-oil sectors with a continued
                      focus on trade, tourism, logistics and hospitality as well
                      as to innovate new sectors which can serve as the pillars
                      of the future growth of the economy while pursuing
                      innovative solutions to move forward in the process of
                      growth and development of the emirate.
                    </p>
                    <p>
                      “This year, Dubai Chamber, will continue its efforts to
                      support the economic vision of the government and do its
                      bit to promote the interests of the business community by
                      exploring new markets, and identifying opportunities for
                      trade and investment.
                    </p>

                    <p>
                      “We will also be continuing with our Global Business Forum
                      series on Africa and Central Asia while focusing on two
                      new regions – the CIS and Latin America. As through these
                      events we provide a platform to businesses and the
                      government from both sides to have a dialogue on areas of
                      mutual cooperation, which is the first step towards
                      building trade and investment linkages while we also
                      support the Dubai Clean Energy Strategy 2050, as part of
                      our efforts to develop the ‘green economy’ and reduce
                      dependence on fossil fuels,” added Buamim.
                    </p>
                    <p>
                      He also said that as part of its strategic objectives to
                      support the development of business and promote Dubai as
                      an international business hub, the Chamber is committed to
                      provide all the support and facilities to the private
                      sector by launching initiatives that will enhance Dubai
                      businesses’ competitiveness including the Dubai Innovation
                      Index (DII), which is in line with the Dubai Plan 2021
                      aiming at making the emirate the first place in the ease
                      of doing business and a favourite destination for
                      investment.
                    </p>
                    <p>
                      Informing about the growth in issuance of ATA Carnets by
                      the Chamber, Buamim informed that the organisation
                      registered a 41.6% rise in the issuance of permission for
                      temporary import of goods as it issued 194 ATA Carnets
                      last year which demonstrated Dubai’s leadership in
                      facilitating global trade, and supporting the conferences
                      and exhibitions industry in the emirate.
                    </p>

                    <p>
                      He also informed that as part of its participation in
                      overseas events, Dubai Chamber last year participated in
                      66 events in 38 cities in 30 countries while the Chamber
                      received 673 delegations from 69 countries and had
                      meetings with 1,600 Government officials and business
                      delegates with a 57% increase in the number of visiting
                      delegations compared to the year 2014.
                    </p>
                    <p>
                      Playing its leading role of settling commercial disputes
                      amicably in the emirate, the Dubai International
                      Arbitration Centre (DIAC) a Dubai Chamber initiative,
                      received 183 arbitration cases in comparison to the 174
                      cases received during the same period in 2014 while Dubai
                      Chamber’s Legal Services department received 555 mediation
                      cases, which together amounted to 738 cases received by
                      the Chamber.
                    </p>

                    <p>
                      Source:{" "}
                      <NavLink
                        to="http://www.emirates247.com/news/emirates"
                        target="_blank"
                      >
                        http://www.emirates247.com/news/emirates
                      </NavLink>
                    </p>
                    <p>
                      Courtesy: <NavLink to="/"> Gulf Jobs In Mumbai</NavLink>
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        {/* Employer Detail END */}
      </div>
      {/* CONTENT END */}
    </div>
  );
};

export default DubaiChamberNewFirms;
